/* CSS Document */
@charset "utf-8";
body{
	margin: 0 auto; /* 居中 */
	padding: 0;
	}


/*--------------------banner-------------------*/
.banner{
	width:100%;
	height: 600px;
	margin: 0 auto;
    background-image: url("redbanner.jpg");
	/*background-color: #012D52*/

}

.search{
width: 550px;
height: 350px;
margin:0 auto 0;
}


.search .first .pas{
width:550px;
height: 50px;
text-align: center;
line-height: 50px;
display: block;
float: left;
font-family: "微软雅黑";
font-size: 25px;
color: white;
margin-top:170px;
}
.search .first{
width: 600px;
height:52px;
}

.search .second{
width: 600px;
height:290px;
}

.search .second .back{
width: 600px;
height:90px;
background-color: hsla(0,0%,100%,0.34);
margin-top: 180px;
}
.search .second .number{
width: 430px;
height: 35px;
margin-top: 25px;
margin-left: 30px;
float:left;
}


.search .second .ok{
width: 100px;
height: 35px;
margin-top: 25px;
/*margin-left: 17px;*/
border: 0;
/*background-color:#1D4F7D;*/
background-color:#94070A;
font-size:20px;
color: white;
font-family: "微软雅黑";
float:left;
}
/*---------------------------关于----------------------*/
.aboutus{
	width:1000px;
	height:420px;
	margin:0 auto;
	margin-bottom: 170px;
	border-bottom: 6px solid #94070A;
	padding-top:50px;
	position:relative;
	}
