.aboutus img{
	width:300px;
	height:300px;
	position:absolute;
	bottom:0;
	}
.intro{
	width:182px;
	height:80px;
	color:#ffffff;
	font-size:30px;
	font-family:"微软雅黑";
	font-weight:bold;
	background-color:#94070A;
	text-align:center;
	line-height:88px;
	position:absolute;
	left:300px;
	bottom:200px;
	z-index:99;/*上下层优先级 数字大在上层*/
	}
.context{
	width:630px;/*800-41-94*/
	height:195px;/*273-*/
	background-color:#f5f5f5;
	position:absolute;
	left:300px;
	bottom:0;
	z-index:1;
	padding:48px 30px 0 41px;
	
	}
.context p{
	font-size:18px;
	line-height:36px;
	color:#333333;
	}

.more{
	width:78px;
	height:33px;
	text-align:center;
	line-height:33px;
	color:#ffffff;
	background-color:#B9B1A1;
	position:absolute;
	right:24px;
	bottom:24px;
	z-index:2;
	font-family:"Arial Black", Gadget, sans-serif;
	cursor:pointer;
	}
/*-----------------------步骤-----------------------*/
.show{
	width:100%;
	height:550px;
	margin:74px auto 0;
	background-color: #F5F5F5;
	padding-top:70px;
	}
.chanpinphoto{
	width: 1200px;
	height:220px;
	margin:54px auto 0;
	}
.chanpinphoto img{
	width:360px;
	float:left;
	margin-left: 20px;
	margin-right:20px;
	cursor:pointer;
	}
.chanpinphoto .step{
	width:80px;
	height:80px;
	float:left;
	margin-left: 159px;
	margin-right:159px;
	border: 1px solid #898989;
    border-radius: 50px;
	text-align: center;
	font-size: 20px;
    color: #484848;
	}
.chanpinphoto p{
	width:220px;
	float:left;
	font-family:"微软雅黑";
	font-size: 15px;
	
	text-align:center;
	margin-left: 90px;
	margin-right: 90px;
	}
