	}
	

.input_class {  /*设置class属性名称为input_class的标签内容*/
    width:250px;
    height:16px;    
}
 
caption{        /*设置caption标签内容*/
    font-size:30px;
    color:#00F;
    font-stretch: wider;
    font-weight: 900; 
}
 
.aaa{          /*设置class属性名称为aaa的标签内容*/
    font-size:16px;
    font-weight: bold;
}


/*---------------------------底部----------------------*/
.bottom{
	width:100%;
	height:100px;
	background:#94070A;
	margin-top: 0px;
	}
	
.down{
	width:1200px;
	height:100px;
	margin:0 auto;
	font-family:"微软雅黑";
	}

.down li{
	list-style:none;	/* 去掉前面的圆点*/
	float:left;			/* 横排显示*/
	width:380px;
	height:100%;
	line-height:100px;
	text-align:center;	/* 居中显示 */
	font-size:18px;
	color:#ffffff;
	font-family:"微软雅黑";
	}

